@import url('https://fonts.googleapis.com/css2?family=Prata&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
}
/* Montserrat   sans-serif */


::-webkit-scrollbar{
    width: 5px;
}

::-webkit-scrollbar-track{
    background-color: #0a192f;
}

::-webkit-scrollbar-thumb{
    background-color: #013f3c7f;
    border-radius: 10px;
}

i{
    cursor: pointer;
}
i:hover{
    color: whitesmoke;
}

/* Loader animation */

.web{
    animation: web 1s infinite;
}

@keyframes web{
    0%{
        opacity:1;
    }
    50%{
        opacity: 0.5;
    }
    100%{
        opacity: 0.5;
    }
}

.app{
    animation: app 1s infinite;
}

@keyframes app{
    0%{
        opacity:0.5;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0.5;
    }
}

.developer{
    animation: developer 1s infinite;
}

@keyframes developer{
    0%{
        opacity:0.5;
    }
    50%{
        opacity: 0.5;
    }
    100%{
        opacity: 1;
    }
}

.adminInput,textarea,.expInput,.projInput,.loginInput{
    height: 40px;
    border: 1px solid gray;
    width: 100%;
    padding-left: 20px;
}
.adminInput:focus,.expInput:focus,.projInput:focus,.loginInput:focus{
    outline: none;
    border: 2px solid gray;
}
textarea{
    height: 100px;
    padding-top: 10px;
}